import React from 'react';

export function CheckBox(props: {
  checked: boolean;
  disabled?: boolean;
  onChange?: (checked: boolean) => void;
}) {
  const { checked, onChange, disabled = false } = props;
  return (
    <input
      type="checkbox"
      disabled={!!disabled}
      checked={checked}
      onChange={(e) => onChange?.(!!e.target.checked)}
      data-cangjie-not-editable
      onKeyDown={e => {
        e.stopPropagation();
        e.preventDefault();
      }}
    />
  );
}
